const REQUIRED_Field = '  ';

export const loginValidation = {
    required: REQUIRED_Field,
    validate: (value: string) => {

        if (value.match(/[--]/)) {
            return '     '
        }

        return true;
    }

}

export const passwordValidation = {
    required: REQUIRED_Field,
    validate: (value: string) => {

        if (value.length < 6) {
            return '    6- '
        }

        return true;
    }

}
